.ant-menu-title-content .app-logo{
  width: 37px;
}
.ant-menu-dark.ant-menu-horizontal .ant-menu-item:first-child {
  color: #fff;
  font-size: 16px;
  pointer-events: none;
}
@keyframes spin {
  from {transform:rotate(0deg);}
  to {transform:rotate(360deg);}
}

.app-logo {
  animation: spin 5s linear 0s infinite;
}
.main-title {
  font-size: 60px;
  line-height: 65px;
  font-weight: 700;
  text-align: center;
}